// 为了避免重复，我们先定义一个通用的角色永久属性结构
const AvatarSchema = z.object({
    current: z.string().prefault(''),
    history: z.array(z.string()).prefault([]),
});
const CharacterStatsSchema = z.object({
    Avatar: AvatarSchema.prefault({ current: '', history: [] }),
    Favor: z.coerce
        .number()
        .transform(v => _.clamp(v, -100, 100))
        .prefault(0),
    Magic: z.coerce.number().prefault(100),
    MagicMax: z.coerce.number().prefault(100),
    Corruption: z.coerce
        .number()
        .transform(v => _.clamp(v, 0, 100))
        .prefault(0),
});
// 接着是角色在场景中的临时状态结构
const SceneCharacterSchema = z.object({
    Thought: z.string().prefault(''),
    Action: z.string().prefault(''),
    State: z.string().prefault('正常'),
    isTransformed: z.boolean().prefault(false),
    Lust: z.coerce
        .number()
        .transform(v => _.clamp(v, 0, 100))
        .prefault(0),
});
// 这是我们将要使用的完整变量结构
export const Schema = z.object({
    当前时间: z.string().prefault('2024年03月17日 星期二 16:45'),
    location: z.string().prefault('远谷町-垂枝樱学园-二年3班教室'),
    个人线: z
        .record(z.string().describe('角色个人线名称'), z.object({
        状态: z.string().prefault('未开始'),
        结局: z.record(z.string().describe('结局名称'), z.any()).prefault({}),
    }))
        .prefault({}),
    // 储存所有角色的永久数据
    角色列表: z.record(z.string().describe('角色名'), CharacterStatsSchema).prefault({}),
    // 储存当前场景中出现的角色的临时状态
    在场角色: z.record(z.string().describe('角色名'), SceneCharacterSchema).prefault({}),
    Enemies: z.record(z.string().describe('敌人名'), z.any()).prefault({}),
    Partners: z.record(z.string().describe('同伴名'), z.any()).prefault({}),
    // 哥哥你的专属数据
    user: z
        .object({
        SkillPoint: z.coerce.number().prefault(10),
        Magic: z.coerce.number().prefault(100),
        MagicMax: z.coerce.number().prefault(100), // 哥哥你的魔力值上限
        Morality: z.coerce
            .number()
            .transform(v => _.clamp(v, -100, 100))
            .prefault(0),
        SkillTree: z
            .record(z.string().describe('技能名'), z.object({
            描述: z.string().prefault(''),
            消耗: z.coerce.number().prefault(0),
            已学习: z.boolean().prefault(false),
        }))
            .prefault({}),
    })
        .prefault({
        SkillPoint: 10,
        Magic: 100,
        MagicMax: 100,
        Morality: 0,
        SkillTree: {},
    }),
});
